body {
    margin: 0;
    padding: 0;
}

::-webkit-scrollbar {
    display: none;
}

.app {
    text-align: center;
    padding-bottom: 7vh;
}

.main-footer {
    position: fixed;
    width: 100vw;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: lightgrey;
}

.input {
    width: 100%;
    margin-left: 10px;
}

.in_footer {
    display: flex !important;
    flex-direction: row !important;
}

.navbar {
    color: white;
    padding-left: 10px;
    padding-right: 10px;
    background-color: #6300eeaf;
    display: flex;
}

.align {
    right: 0;
    position: absolute;
    padding-right: 10px;
}